.question {
   padding: 80px 0;
   &__hr {
      padding: 0;
      height: 0;
      border: none;
      border-top: 1px solid #D9D9D9;
   }
   &__item {
      &-left {
         max-width: 650px;
         padding: 30px 0;
      }
      &-quest {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 20px;
         line-height: 130%;
         color: var(--black-color);
      }
      &-answer {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--black-color);
         width: 0;
         height: 0;
         margin-top: 0;
         overflow: hidden;
         transition: margin-top 0.5s;
      }
   }
   &__items {
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: all 0.5s;
      &.active {
         .question__item-answer {
            margin-top: 10px;
            height: 100%;
            width: 100%;
            white-space: normal;
         }
      }
   }
}