.contacts {
   padding-top: 74px;
   padding-bottom: 80px;
   &__items {
      display: flex;
      gap: 58px;
   }
   &__links {
      display: flex;
      flex-direction: column;
      &-text {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
         margin: 0;
         margin-bottom: 30px;
      }
      &-name {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 20px;
         line-height: 130%;
         color: var(--black-color);
         margin-bottom: 8px;
      }
      &-link {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
         margin-bottom: 30px;
      }
      &-social {
         display: flex;
         flex-direction: row;
         align-items: flex-start;
         padding: 0px;
         gap: 12px;
      }
   }
}