.footer {
   background-color: var(--main-color);
   padding-top: 50px;
   &__menu {
      display: flex;
      gap: 82px;
   }
   &-text {
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--light-color);
      margin: 0;
   }
   &__links {
      display: flex;
      flex-direction: column;
      &-logo {
         margin-bottom: 30px;
      }
      &-inn {
         margin-bottom: 10px;
      }
      &__contacts {
         display: flex;
         flex-direction: column;
         &-label {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
            color: var(--light-color);
            margin-top: 25px;
            margin-bottom: 20px;
         }
         &-name {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 16px;
            line-height: 130%;
            color: var(--light-color);
            margin-bottom: 12px;
         }
         &-link {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--light-color);
            margin-bottom: 20px;
         }
         &-social {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            padding: 0px;
            gap: 12px;
            margin-bottom: 20px;
         }
      }
      &-btn {
         display: flex;
         flex-direction: row;
         justify-content: center;
         align-items: center;
         padding: 10px 45px;
         gap: 10px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--light-color);
         background-color: var(--green-color);
      }
   }
   &__nav {
      &-name {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 20px;
         line-height: 130%;
         color: var(--light-color);
         padding-top: 3px;
         margin-bottom: 50px;
      }
      &__items {
         display: flex;
         flex-wrap: wrap;
         gap: 25px;
      }
      &-list {
         list-style: none;
         display: flex;
         flex-direction: column;
         padding: 0;
         margin: 0;
      }
      &__item {
         &-name {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 16px;
            line-height: 130%;
            color: var(--light-color);
            margin-bottom: 12px;
         }
         &-text {
            max-width: 206px;
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--light-color);
            margin-bottom: 8px;
         }
      }
   }
   &__policy {
      display: flex;
      padding-top: 30px;
      padding-bottom: 50px;
      gap: 269px;
      a {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 130%;
         color: rgba(255, 255, 255, 0.3);
      }
   }
   &__hr {
      margin-top: 45px;
      padding: 0;
      height: 0;
      border: none;
      border-top: 1px solid #183248;
   }
}