.partners {
   padding: 80px 0;
   background-color: var(--main-color);
   &__title {
      color: var(--light-color);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
   }
   &__rectagle {
      width: 278px;
      height: 150px;
      background-color: var(--light-color);
      display: flex;
      justify-content: center;
      align-items: center;
   }
   &__img {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 30px;
   }
   &__text {
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: #000000;
   }
   &__swiper {
      position: relative;
      padding: 0 19px;
      &-next {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         right: 0;
         background-image: url(../img/svg/next.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
         border-radius: 50%;
         border: 1px solid black;
      }
      &-prev {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         left: 0;
         background-image: url(../img/svg/prev.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
         border-radius: 50%;
         border: 1px solid black;
      }
   }
}