.sertificats {
   padding: 80px 0;
   &__title {
      text-align: center;
   }
   &__swiper {
      position: relative;
      padding: 0 21px;
      &-next {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         right: 21px;
         background-image: url(../img/svg/next.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
      &-prev {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         left: 21px;
         background-image: url(../img/svg/prev.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
   }
}