.header {
   &__menu {
      display: flex;
      flex-direction: column;
   }
   &__value {
      display: flex;
      align-items: center;
      margin-top: 20px;
      justify-content: space-between;
   }
   &__phone {
      display: flex;
      align-items: center;
   }
   &__logo {
      margin-right: 124px;
   }
   &__location {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-right: 30px;
      &-text {
         max-width: 124px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 130%;
         color: rgba(48, 48, 48, 0.6);
      }
   }
   &__time {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-right: 80px;
      &-text {
         max-width: 146px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 130%;
         color: rgba(48, 48, 48, 0.6);
      }
   }
   &__phone {
      display: flex;
      gap: 16px;
      &__number {
         max-width: 183px;
         display: flex;
         align-items: center;
         flex-wrap: wrap;
         justify-content: center;
         column-gap: 7px;
         padding-top: 7px;
         &-link {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
            color: var(--black-color);
         }
         &-text {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 12px;
            line-height: 130%;
            color: rgba(48, 48, 48, 0.6);
            margin: 0;
         }
      }
      &__btn {
         display: flex;
         flex-direction: row;
         justify-content: center;
         align-items: center;
         padding: 10px 45px;
         gap: 10px;
         width: 215px;
         height: 58px;
         border: 1.5px solid var(--green-color);
         font-family: 'Gilroy';
         font-weight: 500;
         font-size: 16px;
         line-height: 130%;
         color: var(--green-color);
      }
   }
   &__nav {
      &__items {
         list-style: none;
         display: flex;
         flex-direction: row;
         justify-content: space-between;
         align-items: flex-start;
         margin: 0;
         padding: 0;
         margin-top: 31px;
         gap: 35px;
      }
      &__item {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--black-color);
         display: flex;
         span {
            display: block;
            margin-left: 8px;
            display: flex;
            align-items: center;
         }
      }
      &-burger {
         display: none;
      }
   }
}

.burger { @include burger }